interface Time {
  hours: number;
  minutes: number;
  seconds: number;
}

export const getTimeString = (time: Time): string => {
  if (time.hours === 0) {
    if (time.minutes === 0) {
      return `${time.seconds} .`;
    } else {
      return `${time.minutes} .`;
    }
  } else {
    return `${time.hours} .`;
  }
};
